body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  line-height: 175%;
  font-weight: 400;
  letter-spacing: 0.005em;
  color: #58595b;
  -webkit-font-smoothing: antialiased;
}

h1,
.heading1 {
  font-size: 50px;
  font-family: "EB Garamond", serif;
  font-style: italic;
  line-height: 100%;
  font-weight: 400;
}

p {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  line-height: 175%;
  font-weight: 400;
  letter-spacing: 0.005em;
}

.logo {
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url(./images/logo.png);
  background-size: contain;
  width: 135px;
  height: 135px;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 11;
}

.fullscreen {
  width: 100%;
  height: 600px;
  height: 100vh;
  display: block;
  position: relative;
  overflow: hidden;
  background-color: #f2f2f2;
}

.fullscreen__image-background {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center bottom;
  object-position: center bottom;
}

.container {
  background: #373a47;
}

.menu-wrap a {
  color: #b8b7ad;
}

.menu-wrap a:hover,
.menu-wrap a:focus {
  color: #c94e50;
}

.content-wrap {
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.content {
  position: relative;
  background: #b4bad2;
}

.content::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  content: "";
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
  -webkit-transition: opacity 0.4s, -webkit-transform 0s 0.4s;
  transition: opacity 0.4s, -webkit-transform 0s 0.4s;
  -o-transition: opacity 0.4s, transform 0s 0.4s;
  transition: opacity 0.4s, transform 0s 0.4s;
  transition: opacity 0.4s, transform 0s 0.4s, -webkit-transform 0s 0.4s;
  -webkit-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  -o-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
     transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

/* Menu Button */
.menu-button {
  position: fixed;
  z-index: 1000;
  margin: 1em;
  padding: 0;
  width: 2.5em;
  height: 2.25em;
  border: none;
  text-indent: 2.5em;
  font-size: 1.5em;
  color: transparent;
  background: transparent;
}

.menu-button::before {
  position: absolute;
  top: 0.5em;
  right: 0.5em;
  bottom: 0.5em;
  left: 0.5em;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(20%, #373a47),
    color-stop(20%, transparent),
    color-stop(40%, transparent),
    color-stop(40%, #373a47),
    color-stop(60%, #373a47),
    color-stop(60%, transparent),
    color-stop(80%, transparent),
    color-stop(80%, #373a47)
  );
  background: -webkit-linear-gradient(
    #373a47 20%,
    transparent 20%,
    transparent 40%,
    #373a47 40%,
    #373a47 60%,
    transparent 60%,
    transparent 80%,
    #373a47 80%
  );
  background: -o-linear-gradient(
    #373a47 20%,
    transparent 20%,
    transparent 40%,
    #373a47 40%,
    #373a47 60%,
    transparent 60%,
    transparent 80%,
    #373a47 80%
  );
  background: linear-gradient(
    #373a47 20%,
    transparent 20%,
    transparent 40%,
    #373a47 40%,
    #373a47 60%,
    transparent 60%,
    transparent 80%,
    #373a47 80%
  );
  content: "";
}

.menu-button:hover {
  opacity: 0.6;
}

/* Close Button */
.close-button {
  width: 1em;
  height: 1em;
  position: absolute;
  right: 1em;
  top: 1em;
  overflow: hidden;
  text-indent: 1em;
  font-size: 0.75em;
  border: none;
  background: transparent;
  color: transparent;
}

.close-button::before,
.close-button::after {
  content: "";
  position: absolute;
  width: 3px;
  height: 100%;
  top: 0;
  left: 50%;
  background: #bdc3c7;
}

.close-button::before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
      transform: rotate(45deg);
}

.close-button::after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
      transform: rotate(-45deg);
}

/* Menu */
.menu-wrap {
  position: fixed;
  z-index: 1001;
  width: 300px;
  height: 100vh;
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
  -webkit-transform: translate3d(-400px, 0, 0);
  transform: translate3d(-400px, 0, 0);
  -webkit-transition: -webkit-transform 0.4s;
  transition: -webkit-transform 0.4s;
  -o-transition: transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
  -webkit-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  -o-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
     transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

.menu,
.icon-list {
  height: 100%;
}

.icon-list {
  -webkit-transform: translate3d(0, 100%, 0);
  transform: translate3d(0, 100%, 0);
}

.icon-list a {
  display: block;
  padding: 0.8em;
  -webkit-transform: translate3d(0, 500px, 0);
  transform: translate3d(0, 500px, 0);
}

.icon-list,
.icon-list a {
  -webkit-transition: -webkit-transform 0s 0.4s;
  transition: -webkit-transform 0s 0.4s;
  -o-transition: transform 0s 0.4s;
  transition: transform 0s 0.4s;
  transition: transform 0s 0.4s, -webkit-transform 0s 0.4s;
  -webkit-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  -o-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
     transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

.icon-list a:nth-child(2) {
  -webkit-transform: translate3d(0, 1000px, 0);
  transform: translate3d(0, 1000px, 0);
}

.icon-list a:nth-child(3) {
  -webkit-transform: translate3d(0, 1500px, 0);
  transform: translate3d(0, 1500px, 0);
}

.icon-list a:nth-child(4) {
  -webkit-transform: translate3d(0, 2000px, 0);
  transform: translate3d(0, 2000px, 0);
}

.icon-list a:nth-child(5) {
  -webkit-transform: translate3d(0, 2500px, 0);
  transform: translate3d(0, 2500px, 0);
}

.icon-list a:nth-child(6) {
  -webkit-transform: translate3d(0, 3000px, 0);
  transform: translate3d(0, 3000px, 0);
}

.icon-list a {
  text-decoration: none;
  font-weight: 700;
}

.icon-list a:hover {
  color: #c94e50;
}

/* Shown menu */
.show-menu .menu-wrap {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transition: -webkit-transform 0.8s;
  transition: -webkit-transform 0.8s;
  -o-transition: transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  -webkit-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  -o-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
     transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

.show-menu .icon-list,
.show-menu .icon-list a {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transition: -webkit-transform 0.8s;
  transition: -webkit-transform 0.8s;
  -o-transition: transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  -webkit-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  -o-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
     transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

.show-menu .icon-list a {
  -webkit-transition-duration: 0.9s;
  -o-transition-duration: 0.9s;
     transition-duration: 0.9s;
}

.show-menu .content::before {
  opacity: 1;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
  -webkit-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  -o-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
     transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.panel {
  zoom: 1;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100% auto;
  position: relative;
  display: block;
  width: 100%;
  vertical-align: top;
  font-size: 0;
  line-height: 0;
  overflow: hidden;
}

.panel__toggle {
  position: absolute;
  top: -80px;
  left: 0;
}

.panel--intro-text h1 {
  display: inline-block;
  margin-bottom: 10px;
}

.panel--intro-text {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  text-align: center;
  max-width: 940px;
  padding: 0 20px;
}
@media only screen and (max-width: 768px) {
  .panel--intro-text {
    padding: 0;
  }
  .panel--intro-text h1 {
    padding: 0 15px;
    font-size: 30px;
  }
}

.listing--story {
  text-align: center;
}

.listing a {
  text-decoration: none;
  color: #57585a;
}

.panel--fullwidth {
  width: 100%;
  height: 600px;
  height: 100vh;
  margin-bottom: 30px;
}

.panel__background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  max-width: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center center;
  object-position: center center;
  -o-transition-duration: 6s;
  -webkit-transition-duration: 6s;
  transition-duration: 6s;
  -o-transition-timing-function: x2;
  -webkit-transition-timing-function: x2;
  transition-timing-function: x2;
}

.panel:hover .panel__background {
  -ms-transform: scale(1.08, 1.08);
  -webkit-transform: scale(1.08, 1.08);
  transform: scale(1.08, 1.08);
}

.panel__tag {
  position: absolute;
  bottom: 0;
  left: 0;
  display: table;
  width: 120px;
  height: 120px;
  background: #e8e8e8;
  text-align: center;
}

.panel__tag-text {
  display: table-cell;
  vertical-align: middle;
  font-size: 9px;
  font-family: "Montserrat", sans-serif;
  line-height: 200%;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 3px;
  -webkit-font-smoothing: antialiased;
  color: #a6a8ab;
  padding: 0 20px;
}

.panel--square,
.panel--three-over-two {
  display: inline-block;
  width: 100%;
  margin-bottom: 30px;
}

.panel--square {
  max-width: 380px;
  margin-right: 25px;
}

@media only screen and (max-width: 768px) {
  .panel--square {
    margin-right: 0;
    max-width: 100%;
  }
}

.panel--three-over-two {
  max-width: 560px;
}

.panel--three-over-two:before {
  padding-bottom: 68%;
}

.panel--three-over-two .panel__tag {
  left: initial;
  right: 0;
}

.listing a {
  text-decoration: none;
  color: #57585a;
}

.panel--square:before,
.panel--three-over-two:before {
  content: " ";
  display: block;
}

.panel:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.panel--square:before {
  padding-bottom: 100%;
}

.image-items img {
  margin: 10px;
}

.image-items .small {
  width: calc(50vw - 20px);
  display: inline-block;
  height: auto;
  line-height: 0;
  overflow: hidden;
}

.image-items .large {
  width: calc(100vw - 20px);
  display: inline-block;
  height: auto;
  line-height: 0;
  overflow: hidden;
}

.footer {
  display: block;
  position: relative;
  height: 120px;
}

.footer .l-container {
  font-size: 0;
  line-height: 0;
}

.footer__copyright {
  float: left;
  width: 100%;
  max-width: 520px;
  font-size: 9px;
  font-family: "Futura-bold", sans-serif;
  line-height: 200%;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 3px;
  -webkit-font-smoothing: antialiased;
  margin-top: 54px;
  color: #a6a8ab;
}

.footer__copyright-text {
  display: inline;
}

.footer__link {
  color: #d0d2d3;
  text-decoration: none;
}

.footer__link--reverse {
  color: #a6a8ab;
}

.sharing {
  width: 38%;
  min-width: 200px;
  display: block;
  height: 54px;
  zoom: 1;
}

.footer .sharing {
  width: 50%;
  float: right;
}

.sharing__inner {
  float: right;
  height: 20px;
  padding-top: 30px;
}

.footer .sharing__inner {
  width: auto;
  padding-top: 0px;
  margin-top: 50px;
  float: right;
}

.sharing__title {
  float: left;
  line-height: 18px;
  font-family: "Futura-bold", sans-serif;
  font-size: 9px;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #a6a8ab;
  margin: 0 10px 0 0;
  -ms-transform: translateY(4px);
  -webkit-transform: translateY(4px);
