}

.sharing__list {
  float: left;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.sharing__item {
  float: left;
}

.sharing__link {
  text-decoration: none;
  color: #a6a8ab;
  padding: 0 0 0 20px;
}

.l-margin-top-4 {
  margin-top: 80px;
}

.l-margin-bottom-4 {
  margin-bottom: 80px;
}

.l-margin-top-8 {
  margin-top: 160px;
}

.l-container {
  width: 100%;
  max-width: 1190px;
  -webkit-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0 auto;
  display: block;
  padding-left: 10px;
  padding-right: 10px;
  zoom: 1;
}

.footer .l-container {
  font-size: 0;
  line-height: 0;
}

.sharing__link {
  text-decoration: none;
  color: #a6a8ab;
  padding: 0 0 0 20px;
}

.sharing__link .fab {
  font-size: 24px;
}

a:active,
a:hover {
  outline: 0;
  cursor: pointer;
}

button {
  cursor: pointer;
}

button:hover,
button:focus {
  cursor: pointer;
  outline: none;
  border: none;
}



